import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { async } from '@angular/core/testing';


@Component({
  selector: 'app-parking-manage-parking-slots',
  templateUrl: './parking-manage-parking-slots.component.html',
  styleUrls: ['./parking-manage-parking-slots.component.scss']
})
export class ParkingManageParkingSlotsComponent implements OnInit {
  
  isDataLoaded:boolean = false;

  parkingSlotDataList:any;

  parkingSlotData:any;
  
  slotFieldType:string = "unitno";
  slotData:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  modalService:any;

  apartmentBlockUnitDataList:any;

  slotMainTypeDataList:any;
  slotTypeDataList:any;
  vehicleTypeDataList:any;
  parkingTypeList:any
  

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private parkingService: ParkingService,
    private apartmentService: ApartmentService,
    private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.slotFieldType = type;
    this.slotData = !this.slotData;
  }

  getFieldOrderBy(type) {
    if(this.slotFieldType == type){
      return this.slotData ? 'desc' : 'asc';
    }
    else return '';
  }

  getBlockNo(id){

    var data = _.filter(this.apartmentBlockUnitDataList, function( item ) {
      if(item.apartmentBlockUnitId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].apartmentBlockNumber + " " +  data[0].apartmentBlockUnitNumber
    }

  }

  getSlotMainType(id){

    var data = _.filter(this.slotMainTypeDataList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName
    }

  }

  getSlotType(id){
    var data = _.filter(this.slotTypeDataList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName
    }

  }

  getParkingType(id){

    var data = _.filter(this.parkingTypeList, function( item ) {
      if(item.id === id)
        return item;
    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name
    }


  }

  showConfirmModal(item) {
    this.modalService.showConfirmModal(item);
  }

  ngOnInit() {
    this.getMasterData();
    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {

      if(item != null){

        this.isDataLoaded = false;

        var params = {
          parkingSlotId: item.parkingSlotId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.vehicleService.deleteParkingSlot(params).subscribe((res:any) => {

          this.parkingSlotDataList = this.parkingSlotDataList.filter((type) => type.parkingSlotId !== item.parkingSlotId);
          this.isDataLoaded = true;

        });

        
      }

    });

  }

  /**
   * get Msater Data
   */
  getMasterData(){

    let blockUnitParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.apartmentService.getApartmentBlockUnitByApartmentId(blockUnitParams).subscribe((res:any) => {
      
      this.apartmentBlockUnitDataList = res.filter(item => {
        return item.isActive
      });

    });

    let slotMainParams = {
      LookupTypeId: 71
    }

    //slot main type
    this.lookupService.getLookupValueByLookupTypeId(slotMainParams).subscribe((res:any) => {
      
      this.slotMainTypeDataList = res.filter(item => {
        return item.isActive
      });

    })

    let slotTypeParams = {
      LookupTypeId: 18
    }

    //slot type
    this.lookupService.getLookupValueByLookupTypeId(slotTypeParams).subscribe((res:any) => {
      
      this.slotTypeDataList = res.filter(item => {
        return item.isActive
      });

      console.log(this.slotTypeDataList);
      
    })

    let vehicleTypeParams = {
      LookupTypeId: 2
    }

    //vehicle type
    this.lookupService.getLookupValueByLookupTypeId(vehicleTypeParams).subscribe((res:any) => {
      
      this.vehicleTypeDataList = res.filter(item => {
        return item.isActive
      });

    })
    //parking type
    this.parkingService.getParkingType().subscribe(
      (resp:any) =>{
        this.parkingTypeList = resp;
      },
      error =>{

      }
    )

    this.getParkingSlot();

  }

  /**
   * get parking slot
   */
   getParkingSlot(){

    let parkingSlotParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.vehicleService.getParkingSlotByApartmentId(parkingSlotParams).subscribe((res:any) => {

      this.parkingSlotDataList = res.filter(item => {
        return item.isActive
      });

      this.totalItems = this.parkingSlotDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;

    });
   }

}
