import { Component, OnInit } from '@angular/core';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-parking-create-parking-slot',
  templateUrl: './parking-create-parking-slot.component.html',
  styleUrls: ['./parking-create-parking-slot.component.scss']
})
export class ParkingCreateParkingSlotComponent implements OnInit {
  
  isEditSlot:boolean = false;
  isSlotAdded:boolean = false;
  slotMainTypeList:Array<any>=[];
  slotTypeList:Array<any>=[];
  parkingTypeList:Array<any>=[];
  createSlot:any;
  slotId:any
  isCreateSlot:boolean=true;
  constructor(private apartmentService : ApartmentService,
              private parkingService: ParkingService, 
              private lookupService : LookupService,
              private activatedRoute : ActivatedRoute) { }

  ngOnInit() {
    this.createSlot={
      "parkingSlotId": 0,
      "apartmentId": 0,
      "apartmentBlockUnitId": 0,
      "slotName": "",
      "slotTypeId": 0,
      "slotStatusId": 0,
      "comment": "",
      "isActive": true,
      "insertedBy": 0,
      "insertedOn": "2020-05-09T11:11:03.794Z",
      "updatedBy": 0,
      "updatedOn": "2020-05-09T11:11:03.794Z",
      "parkingTypeId": 0,
      "parkingSlotBlockNumber": 0,
      "sqrFt": 0,
      "slotMainTypeId": 0,
      "permSlotStatusId": 0
    }
    this.getMasterData();
    this.activatedRoute.queryParams.subscribe(params => {
      this.slotId = params['id'];
      if(this.slotId){
        this.isCreateSlot = false;
        this.getParkingSlot();
      }else{
        this.isCreateSlot = true;
      }
    });
  }

  getMasterData(){

    let slotMainList = {
      LookupTypeId: 71
    }

    //slot main type
    this.lookupService.getLookupValueByLookupTypeId(slotMainList).subscribe((res:any) => {
      
      this.slotMainTypeList = res.filter(item => {
        return item.isActive
      });

    })

    let slotTypeParams = {
      LookupTypeId: 18
    }

    //slot type
    this.lookupService.getLookupValueByLookupTypeId(slotTypeParams).subscribe((res:any) => {
      
      this.slotTypeList = res.filter(item => {
        return item.isActive
      });

    })

    //parking type
    this.parkingService.getParkingType().subscribe(
      (resp:any) =>{
        this.parkingTypeList = resp;
      },
      error =>{

      }
    )
  }

  getParkingSlot(){
    this.parkingService.getParkingSlot(this.slotId).subscribe(
      resp =>{
        let slotDetails = resp[0];
        for (const key in slotDetails) {
           this.createSlot[key] = slotDetails[key];
        }
      },
      error =>{
        
      }
    )
  }

  
 /* parkingSlot(){
    if(this.isCreateSlot){
      this.createSlot.insertedBy = 1;
      this.createSlot.updatedBy = 0;
      this.apartmentService.createParkingSlot(this.createSlot).subscribe(
        resp =>{
  
        },
        error =>{
  
        }
      )
    }else{
      this.createSlot.insertedBy = 0;
      this.createSlot.updatedBy = 1;
      this.apartmentService.updateParkingSlot(this.createSlot).subscribe(
        resp =>{
  
        },
        error =>{
  
        }
      )
    }
  }*/
}
